<template>
  <div class="project" @click="openUrl">
    <div class="project__info">
      <h3>{{pr.project.title}}</h3>
    </div>
    <div class="video" @mouseenter="startVideo" @mouseleave="pauseVideo">
      <video-background
          ref="videoBack"
          :src="pr.project.vid"
          :poster="pr.project.post"
          :autoplay="false"
          :style="{backgroundColor: 'black', width: '100%', height: '100%',
           transition: '.5s'}"
      >
      </video-background>
    </div>
    <div class="project__info">
      <p>{{pr.project.body}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProjectForm",
  props:{
    pr: {
      type: Object,
      required: true
    }
  },
  methods: {
    startVideo() {
      this.$refs.videoBack.player.play();
    },
    pauseVideo() {
      this.$refs.videoBack.player.pause();
    },
    openUrl() {
      if (this.pr.project.id === 0) {
        window.open('https://t.me/housel1stbot?start', '_blank').focus();
      }
      else if (this.pr.project.id === 1) {
        window.open('http://localhost:8080', '_blank')
      }
    }
  }
}
</script>

<style scoped>
.project {
  display: flex;
  flex-direction: column;
  width: 40%;
  position: relative;
  border: 2px solid black;
  border-top: 0;
  background-color: rgba(78, 119, 255, 0.8);
  cursor: pointer;
}
.video {
  height: 300px;
}
.project__info {
  width: 100%;
  padding: 10px;
  border-top: 2px solid black;
}

.project__info * {
  color: white;
  padding: 10px;
}

.project__info h3 {
  text-align: center;
}
</style>