<template>
  <div id="container" ref="mainPage">
      <div class="first_page toScroll" ref="firstPage">
    <web-header/>
      </div>
      <div class="second_page toScroll" ref="secondPage">
    <web3-d-model/>
      </div>
        <div class="third_page toScroll" ref="thirdPage">
    <web-i-am-learning/>
        </div>
  </div>
    <web-navigation/>
    <side-my-projects/>
    <side-my-social-media/>
    <contact-me
      ref="contactMe"/>
    <div class="scroll_circles">
      <svg
          v-for="rect in squares"
          :key="rect.id"
          :style="{'opacity': rect.opacity, 'transform': rect.scale, 'cursor': 'pointer'}">
        <rect :id="rect.id" @click="scrollByRects($event)" :style="{'fill': 'white', 'stroke': this.strokeColor}"/>
      </svg>
    </div>
</template>

<script>
import WebHeader from "@/components/WebHeader";
import Web3DModel from "@/components/Web3DModel";
import WebIAmLearning from "@/components/WebIAmLearning";
import WebNavigation from "@/components/UI/WebNavigation";
import SideWindow from "@/components/UI/SideWindow";
import SideMyProjects from "@/components/SideMyProjects";
import ContactMe from "@/components/ContactMe";
import SideMySocialMedia from "@/components/SideMySocialMedia";
export default {
  name: "MainPage",
  components: {
    SideMySocialMedia,
    ContactMe, SideMyProjects, SideWindow, WebNavigation, WebIAmLearning, Web3DModel, WebHeader},
  data() {
    return {
      squares: [
        {
          id: 0,
          scale: "scale(1)",
          opacity: "1",
        },
        {
          id: 1,
          scale: "scale(0.8)",
          opacity: "0.5",
        },
        {
          id: 2,
          scale: "scale(0.8)",
          opacity: "0.5",
        }
      ],
      strokeColor: 'white',
      idOfPages: {'first_page': 0, 'second_page': 1, 'third_page': 2},
      activePanel: null,
      touchStart: 0,
    }
  },
  mounted() {
    this.scrollListener();
    this.observerFunc();
    this.$smoothScroll({
      scrollTo: this.$refs.firstPage,
      hash: '#sampleHash'
    })
  },
  methods: {
    observerFunc(){
      const options = {
        rootMargin: '0px',
        threshold: 1.0
      }
      const observer = new IntersectionObserver(entries => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            this.activePanel = entry.target.classList[0];
            this.squares.forEach((el) => {
              if (this.idOfPages[this.activePanel] === el.id) {
                el.opacity = '1';
                el.scale = 'scale(1)';
              }
              else {
                el.opacity = '0.5';
                el.scale = 'scale(0.8)';
              }
            })
          }
        });
      }, options);
      let target = '.toScroll';
      document.querySelectorAll(target).forEach((i) => {
        if(i) {
          observer.observe(i);
        }
      });
    },
    scrollByRects(e) {
      this.squares.forEach((el) => {
        if (e.target.id == el.id) {
          el.opacity = '1';
          el.scale = 'scale(1)';
        }
        else {
          el.opacity = '0.5';
          el.scale = 'scale(0.8)';
        }
      })
      this.$smoothScroll({
        scrollTo: document.getElementsByClassName('toScroll')[e.target.id],
      })
    },
    firstPageScroll(e) {
      if (e.type === 'wheel') {
        if (e.deltaY > 0) {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 600)
          this.$smoothScroll({
            scrollTo: this.$refs.secondPage,
          })
        }
      }
      else if (e.type === 'keydown') {
        if (e.code === 'ArrowDown') {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 600)
          this.$smoothScroll({
            scrollTo: this.$refs.secondPage,
          })
        }
      }
      else {
        if (this.touchStart > e.changedTouches[0].pageY && this.touchStart-e.changedTouches[0].pageY > 10) {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 600)
          this.$smoothScroll({
            scrollTo: this.$refs.secondPage,
          })
        }
      }
    },
    secondPageScroll(e) {
      if (e.type === 'wheel') {
        this.deleteListener()
        if (e.deltaY > 0) {
          this.$smoothScroll({
            scrollTo: this.$refs.thirdPage,
          })
        }
        else if (e.deltaY < 0) {
          this.$smoothScroll({
            scrollTo: this.$refs.firstPage,
          })
        }
        setTimeout(()=>{this.scrollListener()}, 600)
      }
      else if (e.type === 'keydown') {
        if (e.code === 'ArrowDown') {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 600)
          this.$smoothScroll({
            scrollTo: this.$refs.thirdPage,
          })
        }
        else if (e.code === 'ArrowUp') {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 600)
          this.$smoothScroll({
            scrollTo: this.$refs.firstPage,
          })
        }
      }
      else {
        if (this.touchStart > e.changedTouches[0].pageY && this.touchStart-e.changedTouches[0].pageY > 10) {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 600)
          this.$smoothScroll({
            scrollTo: this.$refs.thirdPage,
          })
        }
        else if (this.touchStart < e.changedTouches[0].pageY && this.touchStart-e.changedTouches[0].pageY < -10) {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 600)
          this.$smoothScroll({
            scrollTo: this.$refs.firstPage,
          })
        }
      }
    },
    thirdPageScroll(e) {
      if (e.type === 'wheel') {
        if (e.deltaY < 0) {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 600)
          this.$smoothScroll({
            scrollTo: this.$refs.secondPage,
          })
        }
      }
      else if (e.type === 'keydown') {
        if (e.code === 'ArrowUp') {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 600)
          this.$smoothScroll({
            scrollTo: this.$refs.secondPage,
          })
        }
      }
      else {
        if (this.touchStart < e.changedTouches[0].pageY && this.touchStart-e.changedTouches[0].pageY < -10) {
          this.deleteListener()
          setTimeout(()=>{this.scrollListener()}, 600)
          this.$smoothScroll({
            scrollTo: this.$refs.secondPage,
          })
        }
      }
    },
    wheelScroll(e) {
      if (this.$refs.contactMe.$el.firstChild.style.opacity !== '1') {
        if (this.activePanel === 'first_page') {
          this.firstPageScroll(e)
        }
        else if (this.activePanel === 'second_page') {
          this.secondPageScroll(e)
        }
        else if (this.activePanel === 'third_page') {
          this.thirdPageScroll(e)
        }
      }
    },
    touchStarted(e) {
      this.touchStart = e.targetTouches[0].pageY;
    },
    scrollListener() {
      this.$refs.firstPage.addEventListener('wheel', this.wheelScroll)
      this.$refs.secondPage.addEventListener('wheel', this.wheelScroll)
      this.$refs.thirdPage.addEventListener('wheel', this.wheelScroll)
      document.addEventListener('keydown', this.wheelScroll)
      document.addEventListener('touchstart', this.touchStarted)
      document.addEventListener('touchend', this.wheelScroll)
    },
    deleteListener() {
      this.$refs.firstPage.removeEventListener('wheel', this.wheelScroll)
      this.$refs.secondPage.removeEventListener('wheel', this.wheelScroll)
      this.$refs.thirdPage.removeEventListener('wheel', this.wheelScroll)
      document.removeEventListener('keydown', this.wheelScroll)
      document.removeEventListener('touchstart', this.touchStarted)
      document.removeEventListener('touchend', this.wheelScroll)
    }
  },
}
</script>

<style scoped>
  .scroll_circles {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 50%;
    right: 0;
    margin: 0 1rem;
    height: 90px;
    width: 20px;
    z-index: 1;
  }
  svg {
    transition: .5s;
    margin-top: 10px;
  }
  rect {
    width: 30px;
    height: 30px;
    stroke-width: 20px;
    transition: .5s;
  }
  @media only screen and (max-width: 450px) {
    rect {
      width: 10px;
      height: 10px;
      stroke-width: 10px;
    }
    .scroll_circles {
      margin: 0;
    }
  }
</style>