<template>
  <div class="container">
    <div class="nav__container" ref="navContainer">
      <img src="@/assets/images/menu_button.png" alt="menu" @click="showMenu" ref="menuBtn">
      <div class="nav_btns_container" ref="navBtnContainer">
        <img src="@/assets/images/cross.png" alt="cross" @click="hideMenu">
        <div class="navigation__tools" ref="navBtns" @click="hideMenu">
          <web-nav-button v-scroll-to="'second_page'">
            WHO AM I
          </web-nav-button>
          <web-nav-button v-scroll-to="'third_page'">
            MY KNOWLEDGE
          </web-nav-button>
          <web-nav-button v-side-surf="'projects'">
            MY PROJECTS
          </web-nav-button>
          <web-nav-button v-side-surf="'education social_media'" @click="arrowsSurf">
            EDUCATION & SOCIAL MEDIA
          </web-nav-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import WebNavButton from "@/components/UI/WebNavButton";
import ProjectForm from "@/components/ProjectForm";
export default {
  name: "WebNavigation",
  components: {ProjectForm, WebNavButton},
  methods: {
    arrowsSurf() {
      document.querySelector('.arrow_button').style.right = '0';
    },
    hideMenu() {
      if (this.$refs.navBtnContainer.style.cssText === 'transform: translateY(0px);') {
        this.$refs.navContainer.style.marginTop = '1vh';
        this.$refs.navBtns.style.marginTop = '2vh';
        this.$refs.navBtnContainer.style.transform = 'translateY(-550px)';
        this.$refs.menuBtn.style.display = 'inline-block';
      }
    },
    showMenu() {
      this.$refs.navBtnContainer.style.transform = 'translateY(0)';
      this.$refs.menuBtn.style.display = 'none';
      this.$refs.navContainer.style.marginTop = '0';
      this.$refs.navBtns.style.marginTop = '0';
    }
  }
}
</script>

<style scoped>
.container {
  position: relative;
}
.nav__container {
  top: 0;
  left: 0;
  margin-top: 1vh;
  width: 100%;
  position: fixed;
}
.nav__container img {
  display: none;
  cursor: pointer;
}

.nav_btns_container {
  transition: all .5s;
}

.nav_btns_container img {
  display: none;
  cursor: pointer;
  position: absolute;
  top: 10px;
  left: 10px;
}

.navigation__tools {
  display: flex;
  width: 100%;
  flex-flow: row wrap;
  text-align: center;
  justify-content: space-around;
  align-items: center;
}

@media only screen and (max-width: 800px) {
  .navigation__tools {
    margin-top: 2vh;
  }
}

@media only screen and (max-width: 450px) {
  .navigation__tools {
    flex-direction: column;
    background-color: black;
    padding: 10px;
  }
  .nav_btns_container {
    background-color: black;
    position: relative;
    transform: translateY(-550px);
  }
  .nav__container img {
    display: block;
  }
}
</style>