          scelerisque odio et, tempor nibh.
        </p>
      </div>
      <div class="my_social_media" ref="socialMedia">
        <h1>
          SOCIAL MEDIA
        </h1>
        <div class="my_certificate">
          <img @click="look_at_cert" src="../assets/images/certificate.svg" class="certificate" alt="certificate">
          <img-show
              v-model:show="showImage">
            <img ref="certImg" src="../assets/images/certificate.svg" alt="certificate">
          </img-show>
          <div class="certificate_text">
            <h2><a class="skillfactory_link" href="https://skillfactory.ru/python-for-web-developers" target="_blank">SKILLFACTORY.RU</a> CERTIFICATE</h2>
            <p>I took this course for 1.5 years. What is taught on it:</p>
            <ul>
              <li v-for="component in skillfactory_list" :key="component">
                {{component}}
              </li>
            </ul>
          </div>
        </div>
        <p class="education_text">
          Lorem ipsum dolor sit amet,
          consectetur adipiscing elit.
          Quisque ornare ultrices vestibulum.
          Proin luctus neque ac tempor elementum.
          Phasellus eu lacus eget arcu bibendum suscipit.
          Mauris sit amet commodo lacus, volutpat ultricies tellus.
          Praesent augue massa, vehicula eget ex eu, elementum mollis mauris.
          Praesent non justo orci. Cras eu lorem convallis, mollis orci non, pellentesque sapien.
          Aenean et velit non metus fringilla varius sit amet vel nisi. Phasellus non leo vehicula,
          scelerisque odio et, tempor nibh.
        </p>
      </div>
      <div class="arrow_button">
        <button class="up_arrow" ref="upBtn"
                @click="upScrollBtn"
                @mouseenter="scaleUpArr" @mouseleave="unScaleUpArr">
      <span class="up_arr btn_icon" ref="upArr">

      </span>
        </button>
        <button class="down_arrow" ref="downBtn"
                @click="scrollDownBtn"
                @mouseenter="scaleDownArr" @mouseleave="unScaleDownArr">
      <span class="down_arr btn_icon" ref="downArr">

      </span>
        </button>
      </div>
    </div>
  </side-window>
</template>

<script>
import SideWindow from "@/components/UI/SideWindow";
import ImgShow from "@/components/UI/ImgShow";
export default {
  name: "SideMySocialMedia",
  components: {ImgShow, SideWindow},
  data() {
    return {
      skillfactory_list: [' HTML and CSS', ' JavaScript and Bootstrap', ' Python', ' SQL', ' Django', ' Linux',
        ' Working with freelance orders'],
      showImage: false,
      activePanel: null,
    }
  },
  mounted() {
    this.scrollListenerSide();
    this.$refs.upBtn.style.background = 'rgba(33, 33, 33, 0.2)'
    this.$refs.upBtn.disabled;
  },
  methods: {
    scrollDownBtn() {
      this.$smoothScroll({
        container: this.$refs.containerScroll,
        scrollTo: this.$refs.socialMedia,
      })
      this.$refs.downBtn.style.background = 'rgba(33, 33, 33, 0.2)'
      this.$refs.upBtn.style.background = 'rgba(33, 33, 33, 0.8)'
      this.$refs.downBtn.disabled = true;
      this.$refs.upBtn.disabled = false;
    },
    upScrollBtn() {
      this.$smoothScroll({
        container: this.$refs.containerScroll,
        scrollTo: this.$refs.myEducation,
      })
      this.$refs.upBtn.style.background = 'rgba(33, 33, 33, 0.2)'
      this.$refs.downBtn.style.background = 'rgba(33, 33, 33, 0.8)'
      this.$refs.upBtn.disabled = true;
      this.$refs.downBtn.disabled = false;
    },
    scaleUpArr() {
      this.$refs.upArr.style.transform = 'scale(0.9)';
    },
    unScaleUpArr() {
      this.$refs.upArr.style.transform = 'scale(0.8)';
    },
    scaleDownArr() {
      this.$refs.downArr.style.transform = 'scale(0.9)';
    },
    unScaleDownArr() {
      this.$refs.downArr.style.transform = 'scale(0.8)';
    },
    look_at_cert() {
      this.showImage = true;
    },
    scrollListenerSide() {
      this.$refs.socialMedia.addEventListener('wheel', this.socialScroll)
      this.$refs.myEducation.addEventListener('wheel', this.educationScroll)
    },
    deleteListenerSide() {
      this.$refs.socialMedia.removeEventListener('wheel', this.socialScroll)
      this.$refs.myEducation.removeEventListener('wheel', this.educationScroll)
    },
    educationScroll(e) {
      if (e.deltaY > 0) {
        this.deleteListenerSide();
        this.$smoothScroll({
          container: this.$refs.containerScroll,
          scrollTo: this.$refs.socialMedia,
        })
        this.$refs.downBtn.style.background = 'rgba(33, 33, 33, 0.2)'
        this.$refs.upBtn.style.background = 'rgba(33, 33, 33, 0.8)'
        this.$refs.downBtn.disabled = true;
        this.$refs.upBtn.disabled = false;
        setTimeout(()=>{this.scrollListenerSide()}, 600)
      }
    },
    socialScroll (e) {
      if (e.deltaY < 0) {
        this.deleteListenerSide();
        this.$smoothScroll({
          container: this.$refs.containerScroll,
          scrollTo: this.$refs.myEducation,
        })
        this.$refs.upBtn.style.background = 'rgba(33, 33, 33, 0.2)'
        this.$refs.downBtn.style.background = 'rgba(33, 33, 33, 0.8)'
        this.$refs.upBtn.disabled = true;
        this.$refs.downBtn.disabled = false;
        setTimeout(()=>{this.scrollListenerSide()}, 600)
      }
    },

  }
}
</script>

<style scoped>
  .social_media {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    overflow-y: hidden;
    height: 100%;
  }

  .skillfactory_link {
    text-decoration: none;
    color: black;
  }

  .my_education, .my_social_media {
    padding: 10% 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    width: 100%;
  }

  .my_education {
    background-color: rgb(54, 227, 54);
  }

  .my_social_media {
    background-color: darkorange;
  }

  .my_education h1:first-child, .my_social_media h1:first-child {
    font-size: 60px;
  }

  .my_certificate {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 70%;
  }

  .certificate_text {
    display: flex;
    flex-direction: column;
  }

  .certificate_text h2 {
    display: flex;
    font-size: 40px;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    color: orangered;
  }

  .certificate_text h2 * {
    color: orangered;
    transition: .5s;
  }

  .certificate_text h2  *:hover {
    text-shadow: 2px 2px #888888;
    color: darkgreen;
  }

  .certificate_text p {
    margin-top: 5px;
    font-size: 23px;
    margin-bottom: 5px;
  }

  .certificate_text ul {
    font-size: 21px;
    list-style-type: none;
  }

  .certificate_text ul li {
    text-align: center;
  }

  .certificate {
    width: 500px;
    height: 400px;
    cursor: pointer;
    transition: .7s;
  }

  .education_text {
    margin-top: 50px;
    width: 70%;
    text-align: center;
    font-size: 20px;
  }

  .arrow_button {
    position: fixed;
    transition: .7s;
    display: flex;
    flex-direction: column;
    right: -150%;
    bottom: 1.5rem;
    z-index: 3;
    text-align: center;
    margin: 1rem;
  }

  .arrow_button button {
    position: relative;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    display: block;
    background: red;
    transition: .3s;
    border-radius: 50%;
    width: 3.6rem;
    height: 3.6rem;
    outline: none;
    border: 0;
    cursor: pointer;
    background: rgba(33,33,33, 0.8);
    color: #fefefe;
    margin: 1.2rem auto 0;
  }

  .up_arr {
    background-image: url("@/assets/images/up_arrow.png");
    background-position: 50%;
  }

  .down_arr {
    background-image: url("@/assets/images/down_arrow.png");
    background-position: 50%;
  }

  .btn_icon {
    display: block;
    height: 2.1rem;
    line-height: 2.4rem;
    background-size: 70%;
    background-repeat: no-repeat;
    transition: .5s;
    transform: scale(0.8);
  }

</style>