<template>
  <div class="image" v-if="show" @click="hideDialog">
    <div @click.stop class="image__content" :style="{transform: 'scale('+imgSize+')'}" ref="img" @wheel="changeSize">
      <slot>

      </slot>
    </div>
  </div>
</template>

<script>
import toggleMixin from "@/mixins/toggleMixin";

export default {
  name: 'img-show',
  mixins: [toggleMixin],
  data() {
    return {
      imgSize: 1,
    }
  },
  methods: {
    changeSize(e) {
      if (1.7 < this.imgSize && -e.deltaY/1000 > 0) {}
      else if (this.imgSize < 0.5 && -e.deltaY/1000 < 0) {}
      else {
        this.imgSize += -e.deltaY/1000
      }
    }
  }
}
</script>

<style scoped>
.image {
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  display: flex;
}

.image__content {
  margin: auto;
  background: white;
  border-radius: 12px;
  padding: 20px;
  transition: .2s;
}

.image__content * {
  width: 100%;
  height: 100%;
}
</style>