export default {
    mounted(el, binding) {
        el.addEventListener('click', ()=>{
            const to_surf = document.getElementsByClassName('side')
            const arrow = document.getElementsByClassName('back_arrow')
            let count = 0;
            for (let s of to_surf) {
                if (s.children[0].className === binding.value) {
                    s.style.left = '10%';
                    arrow[count].style.left = 0;
                }
                count ++;
            }
        })
    },
    name: 'side-surf'
}