<template>
  <div class="contact_me">
    <div class="contact_window" ref="contactWindow" :style="{opacity: this.opacityWindow, right: this.rightWindow}">
      <div class="chat" ref="chatPanel">

      </div>
    </div>
    <div class="send_message_panel" ref="sendMessagePanel" :style="{opacity: this.opacityWindow, right: this.rightSendPanel}">
      <input type="text" id="message" ref="sendInput" placeholder="Type a message: " v-model="msg">
      <button class="btn send_message" @click="sendMessage" disabled ref="btn_send">Send</button>
    </div>
    <img id="contactMeImg" src="../assets/images/man.png" alt="" width="50" height="50" @click="showWindow">
  </div>
</template>

<script>
export default {
  name: "ContactMe",
  data() {
    return {
      msg: '',
      scrollBarMinHeight: 0,
      opacityWindow: '0',
      counterClick: 1,
      rightWindow: '-100%',
      rightSendPanel: '-100%',
      scrollSendPanel: 87
    }
  },
  mounted() {
    document.addEventListener('keypress', (e)=>{
      if (e.code === 'Enter') {
        if (e.target === this.$refs.sendInput) {
          if (this.msg.trim().length !== 0) {
            this.sendMessage();
          }
        }
      }
    })
  },
  methods: {
    sendMessage() {
      let message_side = 'flex-end';
      this.writeToScreen(`${this.msg}`, message_side)
      this.msg = '';
    },
    writeToScreen(message, side) {
    const time_now = new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    let pre = document.createElement("div");
    pre.style.display = 'flex';
    pre.style.justifyContent = side;
    let span = document.createElement('span');
    span.style.maxWidth = '75%';
    span.style.wordWrap = 'break-word';
    span.style.padding = '5px 10px';
    span.style.marginTop = '10px';
    span.style.borderRadius = '6px';
    span.innerHTML = message;
    let time = document.createElement('span');
    time.style.opacity = '0.5';
    time.style.display = 'flex';
    time.style.alignItems = 'flex-end';
    time.style.fontSize = '12px';
    time.innerHTML = time_now;
    if (side === 'flex-end') {
      span.style.border = '3px solid darkgreen'
      time.style.marginRight = '5px';
      pre.appendChild(time);
      pre.appendChild(span);
    }
    else {
      span.style.border = '3px solid darkred'
      time.style.marginLeft = '5px';
      pre.appendChild(span);
      pre.appendChild(time);
    }
    this.$refs.chatPanel.appendChild(pre);
   },

    showWindow() {
      if (this.counterClick) {
        this.rightWindow = '5%';
        this.rightSendPanel = '5%';
        this.opacityWindow = '1';
        this.counterClick = 0;
      }
      else {
        this.rightWindow = '-100%';
        this.rightSendPanel = '-100%';
        this.opacityWindow = '0';
        this.counterClick = 1;
      }
    }
  },
  watch: {
    msg() {
      this.$refs.btn_send.disabled = this.msg.trim().length === 0;
    }
  }
}
</script>

<style scoped>
.contact_me {
  position: fixed;
  width: 30%;
  height: 30%;
  bottom: 0;
  right: 0;
  z-index: 2;
}
.contact_window, .send_message_panel {
  position: fixed;
  transition: .5s;
  width: 100%;
  min-width: 250px;
  max-width: 400px;
  z-index: 2;
  background-color: white;
}
.contact_window {
  border: 3px solid deepskyblue;
  bottom: 5%;
  padding: 10px;
  overflow-y: auto;
  overflow-x: hidden;
  height: 30%;
  min-height: 400px;
}

.send_message_panel {
  height: 6%;
  bottom: 5.3%;
  padding: 6.5px 0;
  background-color: white;
  border-right: 3px solid deepskyblue;
  border-left: 3px solid deepskyblue;
  display: flex;
  justify-content: space-around;
  max-height: 55px;
}

.contact_window::-webkit-scrollbar {
  display: none;
}

#message {
  border-radius: 5px;
  border: 2px solid darkgreen;
  padding: 10px;
  width: 55%;
}
.chat {
  margin-bottom: 20%;
}
.btn {
  border: none;
  outline: none;
  -webkit-tap-highlight-color: transparent;
  box-shadow: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 15px;
  border-radius: 3px;
  font-size: 12px;
  text-transform: uppercase;
  width: 30%;
  color: white;
  background: #315efb;
  transition: 0.3s;
}

.btn:hover {
  box-shadow: 0 2px 8px 2px rgba(141,150,178,.3);
  transform: scale(1.05);
}

button:disabled,
button[disabled]{
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
}

#contactMeImg {
  position: absolute;
  bottom: 5%;
  right: 5%;
  cursor: pointer;
  z-index: 1;
}

@media only screen and (max-width: 450px) {
  .contact_window, .send_message_panel {
    max-width: 200px;
  }
}
@media only screen and (max-width: 275px) {
  .contact_window, .send_message_panel {
    min-width: 220px;
  }
}
@media only screen and (max-width: 650px) {
  .contact_window {
    bottom: 8%;
  }
  .send_message_panel {
    bottom: 8.3%;
  }
  #contactMeImg {
    width: 40px;
    height: 40px;
  }
}
</style>