<template>
  <div class="container">
    <video-background
        :src="require('@/assets/bgvideo/webKnowlendgeVideo.mp4')"
        style="height: 100vh; position: relative"
        overlay="rgba(0, 0, 0, 0.5)"
    >
      <div class="knowledge">
        <h1 id="whatAmI" v-intersection="Anim">WHAT AM I LEARNING</h1>
        <div class="look_at_me">
          <ul class="list_knowledge"
              v-for="li in knowledge"
              :key="li.id"
              :style="{'transform': li.transform, color: li.color, fontWeight: '600', 'opacity': li.opacity}">
            <li class="know_list" :style="li.backgroundImg">{{li.title}}</li>
          </ul>
        </div>
      </div>
      <div class="observer"></div>
    </video-background>
  </div>
</template>

<script>
export default {
  name: "WebIAmLearning",
  data() {
    return {
      knowledge: [
        {
          id: 0,
          backgroundImg: {backgroundImage: `url(${require('@/assets/images/knowledge/python.png')})`},
          transform: 'translateX(20vw)',
          color: 'black',
          opacity: '0',
          title: 'Python'
        },
        {
          id: 1,
          backgroundImg: {backgroundImage: `url(${require('@/assets/images/knowledge/JavaScript.png')})`},
          transform: 'translateX(-20vw)',
          opacity: '0',
          color: 'black',
          title: 'JavaScript'
        },
        {
          id: 2,
          backgroundImg: {backgroundImage: `url(${require('@/assets/images/knowledge/html.png')})`},
          transform: 'translateX(20vw)',
          opacity: '0',
          color: 'rgb(238,96,24)',
          title: 'HTML'
        },
        {
          id: 3,
          backgroundImg: {backgroundImage: `url(${require('@/assets/images/knowledge/css.png')})`},
          transform: 'translateX(-20vw)',
          opacity: '0',
          color: 'rgb(0,106,190)',
          title: 'CSS'
        },
        {
          id: 4,
          backgroundImg: {backgroundImage: `url(${require('@/assets/images/knowledge/django.png')})`},
          transform: 'translateX(20vw)',
          opacity: '0',
          color: 'rgb(0,58,43)',
          title: 'Django'
        },
        {
          id: 5,
          backgroundImg: {backgroundImage: `url(${require('@/assets/images/knowledge/Vue.png')})`},
          transform: 'translateX(-20vw)',
          opacity: '0',
          color: 'rgb(64,183,130)',
          title: 'Vue.js'
        },
        {
          id: 6,
          backgroundImg: {backgroundImage: `url(${require('@/assets/images/knowledge/git.png')})`},
          transform: 'translateX(20vw)',
          opacity: '0',
          color: 'rgb(224,66,41)',
          title: 'Git'
        },
      ]
    }
  },
  methods: {
    Anim() {
      let counter = 0;
      this.knowledge.forEach((e) => {
        e.transform = 'translateX(0)'
        e.opacity = '1';
        counter ++;
      })
    },
  },
}
</script>

<style scoped>
.knowledge {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100%;
}

.look_at_me {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 65%;
  margin-top: -20vh;
  height: 100%;
}

.list_knowledge {
  min-width: 220px;
  letter-spacing: 1px;
  cursor: default;
  font-size: 25px;
  transition: .7s;
}

.know_list {
  list-style-type: none;
  background-color: white;
  background-position: 2% center;
  background-size: 20% 80%;
  background-repeat: no-repeat;
  text-align: center;
  padding: 10px 50px;
  font-size: 21px;
  margin: 5px 0;
  border-radius: 7px;
  transition: .7s;
}

.know_list:hover {
  transform: scale(1.1);
}
.knowledge h1 {
  color: #db3a3a;
  font-size: 88px;
  margin-top: 10vh;
}

@media only screen and (max-width: 960px) {
  .knowledge h1 {
    font-size: 50px;
    margin-top: 20vh;
    text-align: center;
  }
}
@media only screen and (max-width: 450px) {
  .knowledge h1 {
    font-size: 44px;
    margin-top: 10vh;
  }
  .look_at_me {
    margin-top: 1px;
  }
}
@media only screen and (min-height: 1200px){
  .look_at_me {
    max-height: 1080px;
  }
}
@media only screen and (max-height: 750px) {
  .look_at_me {
    height: 75%;
    margin-top: 1px;
  }
}
@media only screen and (max-height: 750px) {
  .look_at_me {
    margin-top: 0;
  }
}
</style>